<template>
    <div>
        <div>
          <div v-if="particle" style="height:100%;width:100%;position:absolute;z-index:10000;" id="particlesjs3" ></div>
          <div style="" 
            class="d-flex justify-content-center align-items-center shadow-lg rounded-lg">
            <br>
                <div style="width:100%" class=" ">
                  <div class="row justify-content-center" v-for="(item, index) in $store.state.data.acara" :key="index+'acara'">
                      <div class="col-10 text-center" style="line-heigth:0.5;">
                          <curve-text :width="250" :height="80" :r="60" offset="50%" textid="my-curve-text" 
                          style="margin-bottom:-40px;font-size:19px;letter-spacing:1px;font-family: 'Kaisei HarunoUmi', serif;display:block;margin:auto;"
                          class="font-semibold text-center"
                          >{{$store.state.data.undangan.curve}}</curve-text>
                          <!-- pilih -->
                          <div class="" style="position:relative;width:100%;" 
                            :style="`${$store.state.data.undangan.pilih=='nikah'?'margin-top:-35px;':'margin-top:-5px;'}`"
                          >
                          <!-- pilih -->
                            <p class=" text-4xl  text-center wow" style="letter-spacing: 2px;word-spacing: 2px;font-family: 'Alex Brush', cursive;"
                              :class="`${$store.state.data.undangan.animation=='random'?$store.state.animrandom[Math.floor(Math.random()*$store.state.animrandom.length)]:$store.state.data.undangan.animation}`"
                            >
                              {{item.judul_acara}}
                          </p>
                            <p class="text-center font-semibold text-xl wow" style="font-family: 'Cinzel Decorative', cursive;"
                              :class="`${$store.state.data.undangan.animation=='random'?$store.state.animrandom[Math.floor(Math.random()*$store.state.animrandom.length)]:$store.state.data.undangan.animation}`"
                            >
                              {{format(item.tanggal_acara)}}
                          </p>
                          <p class="text-center font-semibold text-lg wow"
                          :class="`${$store.state.data.undangan.animation=='random'?$store.state.animrandom[Math.floor(Math.random()*$store.state.animrandom.length)]:$store.state.data.undangan.animation}`"
                          >
                              {{item.jam_acara}}
                          </p>
                          <p class="text-center font-semibold text-sm wow"
                              :class="`${$store.state.data.undangan.animation=='random'?$store.state.animrandom[Math.floor(Math.random()*$store.state.animrandom.length)]:$store.state.data.undangan.animation}`"
                          >
                              Lokasi Acara
                          </p>
                          <p class="text-center wow text-lg font-semibold"
                              :class="`${$store.state.data.undangan.animation=='random'?$store.state.animrandom[Math.floor(Math.random()*$store.state.animrandom.length)]:$store.state.data.undangan.animation}`"
                            v-html="item.alamat_acara">
                          </p>
                          <hr class="style17" style="">
                          </div>
                      </div>
                  </div>
                  <!--  -->
                </div>
            </div>
        
        </div>
    </div>
</template>
<script>
import 'particles.js'
var id = require("date-fns/locale/id");
import CurveText from '@inotom/vue-curve-text';
export default{
 components: {
        CurveText,
    },
  props:['particle'],
    mounted() {
        window.particlesJS("particlesjs3", this.$store.state[this.$store.state.data.undangan.particle])
    },
    methods: {
         format(tgl) {
            return this.$datefns.format(
                new Date(tgl),
                "dddd, DD MMMM YYYY",
                 {
                  locale: id,
                }
            )
        },
    },
}
</script>