                  <!-- <span class="typcn typcn-home text-2xl p-1"></span> -->
                  <p class="text-xs" style="font-size:10px;">Undangan</p>
                </a>
              </div>
              <div class="col-2 text-center">
                <a class="scrollactive-item hover:text-orange-400"  style="text-decoration:none;" href="#mempelai">
                  <i class="fas fa-user-friends text-2xl p-1"></i>
                  <!-- <span class="typcn typcn-group text-2xl p-1"></span> -->
                  <p class="text-xs" style="font-size:10px;" v-if="$store.state.data.undangan.pilih=='nikah' || $store.state.data.undangan.pilih=='aqiqah'">Mempelai</p>
                  <p class="text-xs" style="font-size:10px;" v-if="$store.state.data.undangan.pilih=='ultah'">Ultah</p>
                  
