                    <p class="text-xs" style="font-size:10px;">Galery</p>
                  </a>
              </div>
               <div class="col-2 text-center">
                 <a class="scrollactive-item hover:text-orange-400"  style="text-decoration:none;" href="#comment">
                   <i class="fas fa-comments text-2xl p-1"></i>
