</template>
<script>
var temp;
export default{
  props:['particle'],
  data(){
    return{
      play:true
    }
  },
  methods: {
    onItemChanged(a,curr,next){
      curr.focus()
    },
    playAudio(){
      var audio = document.getElementById("audio1");
      this.play=!this.play;
      if(!this.play){
        audio.play();
      }else{
        audio.pause();
        audio.currentTime = 0;
      }
    }
  },
  mounted() {
    let that=this
    if(this.$store.state.data.interact==0){
      this.$router.push('/')
    }else{
      that.playAudio();
    }
    var style = document.createElement('style');
    console.log('undangan',this.$store.state.data.undangan)
    style.innerText=`
    a:link {
      color:${this.$store.state.data.undangan.navigasicolor};
    }
    a:visited  {
      color:${this.$store.state.data.undangan.navigasicolor};
    }
    a:focus {
      color:lightpink;
    }
    `;
    document.body.appendChild(style);
    // document.addEventListener('click',function(){
    //   if(interack==0){
    //     that.playAudio();
    //     interack++;
    //   }
    // })
  },
}
